import { useState } from "react";

import { Link } from "react-router-dom";

const Admit = ()=>{
    const [username, setUsername] = useState('');
    const [psw, setPsw] = useState('');

    async function login(event){
        event.preventDefault()
       const response= await fetch("http://localhost:5001/api/admin",
        {
            method:'POST',
            headers:{
                'Content-Type': 'application/json'    
            },
            body: JSON.stringify({
                username,
                psw,
            }),
            
        })
        const data= await response.json();
        // console.log(data)

        if(data.user){
            localStorage.setItem('token',data.user)
            alert("Login Successful");
            window.location.href="/dashboard";
        }
        else{
            alert("Please check email or password");
        }
        
    }

    return(
        <>
            <div className="container text-center">
            <p className="text-center display-6"> Admin Panel</p>
            <form className="center loginform" onSubmit={login}>
                <div className="form-floating mb-3">
                    <input type="text" className="form-control" id="floatingInput" placeholder="abc"
                    value={username}
                    onChange={(e)=> setUsername(e.target.value)} />
                    <label htmlFor="floatingInput">Username</label>
                </div>
                <div className="form-floating">
                    <input type="password" className="form-control" id="floatingPassword" placeholder="Password"
                    value={psw}
                    onChange={(e)=> setPsw(e.target.value)}/>
                    <label htmlFor="floatingPassword">Password</label>
                </div>
                <button type="submit" className="btn admitbtn" >Login</button>
                </form>
                </div>
        </>
    )
}
export default Admit;